import moment from 'moment';
import React, { useEffect, useState } from 'react';
import DatePicker from 'react-datepicker';
import { connect, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Select from 'react-select';
import Form from 'react-validation/build/form';
import { Calendar } from '../../../../assets/images/icons';
import { setProcessModule, setCurrentTask } from '../../../../reduxStore/actions/flowActions';
import { store } from '../../../../reduxStore/store';
import { createTask, deleteTask, updateTask, startTask } from '../../../../services/tasks.service';
import { DropdownIndicator, priorityArray } from '../../../../shared/utility';
import { ModalR } from '../../../common';
import Button from '../../../common/UI/Button/Button';
import cls from './TaskForm.module.scss';
import { isMobile } from 'react-device-detect';
import MessageContainer from '../../ProcessesComps/MessageContainer/MessageContainer';
const R = require('ramda');
function TaskForm(props) {
	const navigate = useNavigate();
	const { selectedProcess, isProcess, setIsProcessBegin, companyUsersArray, task, isEdit, assigneeId, setDisplayModal, allModules, companyProcesses, onClose, user } = props;

	const [newAssignee, setNewAssignee] = useState(task?.assignee?.id ? { assignee_id: task?.assignee?.id } : '');
	const [taskProcess, setTaskProcess] = useState(task?.process?.id ? { process: task?.process?.id } : '');
	const [taskName, setTaskName] = useState(task?.name || '');
	const [taskStatus, setTaskStatus] = useState(task?.status || '');
	const [taskDescription, setTaskDescription] = useState(task?.description || '');
	const [dueDate, setDueDate] = useState(task?.due_date ? new Date(task?.due_date) : new Date().setDate(new Date().getDate() + 1));
	const [message, setMessage] = useState('');
	const [loading, setLoading] = useState(false);
	const [loadingRemove, setLoadingRemove] = useState(false);
	const [mounted, setMounted] = useState(false);
	const [toDeleteTask, setToDeleteTask] = useState(false);
	const [isModuleTask, setIsModuleTask] = useState(isProcess);
	const [priority, setPriority] = useState(task?.add_info?.priority || '');
	const usersList =
		companyUsersArray?.length > 0
			? companyUsersArray.map(elem => {
					return { label: elem.first_name + ' ' + elem.last_name, value: elem.id };
			  })
			: [];

	const modulesList =
		allModules?.length > 0
			? allModules.map(elem => {
					return { label: elem.name, value: elem.id, module: elem };
			  })
			: [];
	useEffect(() => {
		setMounted(true);
		return () => {
			setMounted(false);
		};
	}, []);

	useEffect(() => {
		if (isModuleTask && selectedProcess?.id && mounted) {
			const setInitials = () => {
				setTaskProcess({ procedure_id: selectedProcess.id });
				setTaskDescription(selectedProcess.description);
			};
			setInitials();
		}
	}, [isModuleTask, mounted, selectedProcess.description, selectedProcess.id]);

	const beginProcess = () => {
		//console.log('setProcessTask(task', task);

		store.dispatch(setCurrentTask(task));
		store.dispatch(
			setProcessModule({
				...companyProcesses.find(p => p.id === task.process.id),
				stepIndex: 0,
				module_id: task.add_info.module_id,
				module: allModules.find(m => m.id === task.add_info.module_id),
			})
		);
		navigate('/begin-module');
	};

	const addTask = toStart => {
		setLoading(true);
		createTask(
			taskProcess !== ''
				? {
						newAssignee,
						taskProcess: taskProcess,
						taskName,
						taskDescription,
						dueDate: moment(dueDate).format('YYYY-MM-DD'),
						add_info: {
							module_id: taskProcess.module.id,
							priority: priority,
						},
				  }
				: {
						newAssignee,
						taskName,
						taskDescription,
						dueDate: moment(dueDate).format('YYYY-MM-DD'),
						add_info: {
							priority: priority ?? '',
						},
				  }
		).subscribe({
			next: data => {
				if (toStart) {
					startTask(data.id).subscribe();
				}

				setNewAssignee('');
				setTaskProcess('');
				setTaskName('');
				setTaskDescription('');
				setDueDate(new Date());

				store.dispatch(setCurrentTask(data));
				store.dispatch(
					setProcessModule({
						...taskProcess,
						stepIndex: 0,
						module_id: taskProcess.module.id,
						module: taskProcess.module,
					})
				);

				if (isModuleTask) {
					navigate('/begin-module');
					setIsProcessBegin(true);
				}
				if (toStart) {
					navigate('/begin-module');
				}
			},
			error: error => {
				if (error) {
					setMessage(error.data);
					setLoading(false);
				}
			},
			complete: () => {
				setLoading(false);
				!isModuleTask && setDisplayModal(false);
			},
		});
	};

	const customStyles = {
		control: (provided, _state) => ({
			...provided,
			boxShadow: 'none',
			'&:hover': {
				border: '1px solid var(--app-color-gray-lighter)',
			},
			position: 'relative',
			height: '60px',
			width: '100%',
			border: 'var(--app-color-gray-lighter) solid 1px',
			padding: '3px',
			fontFamily: 'var(--app-font-quicksand-medium)',
			fontSize: '1.4em',
			fontWeight: '700',
			color: '#4A4A4A',
			textAlign: 'left',
			flex: '1 1 auto',
			justifyContent: 'center',
			alignItems: 'center',
			alignContent: 'center',
			alignSelf: 'center',
			margin: '10px 0 0 0 ',
			marginBottom: '20px',
		}),
		input: (provided, _state) => ({
			...provided,
			fontFamily: 'var(--app-font-quicksand-medium)',
			color: '#4A4A4A',
			width: '100%',
			textAlign: 'left',
			fontSize: '0.8em',
			fontWeight: '700',
			alignSelf: 'center',
		}),
		menu: (provided, _state) => ({
			...provided,
			justifyContent: 'center',
			alignItems: 'center',
			verticalTextAlign: 'center',
			alignContent: 'center',
		}),
		menulist: (provided, _state) => ({
			...provided,
			justifyContent: 'center',
			alignItems: 'center',
			verticalTextAlign: 'center',
			alignContent: 'center',
			fontFamily: 'var(--app-font-quicksand-medium)',
		}),
		option: (provided, _state) => ({
			...provided,
			minHeight: 15,
			paddingTop: 0,
			fontSize: '1.1em',
			color: 'black',
			verticalTextAlign: 'center',
			justifyContent: 'center',
			alignItems: 'center',
			alignContent: 'center',
			fontFamily: 'var(--app-font-quicksand-medium)',
		}),
		placeholder: (provided, _state) => ({
			...provided,
			fontSize: '0.75em',
			lineHeight: '20px',
			color: '#4A4A4A',
			fontWeight: 400,
			overflow: 'hidden',
			fontFamily: 'var(--app-font-quicksand-medium)',
		}),
		singleValue: (provided, state) => {
			const opacity = state.isDisabled ? 0.5 : 1;
			const transition = 'opacity 300ms';
			return {
				...provided,
				opacity,
				transition,
				marginLeft: '0px',
				fontSize: '0.75em',
				lineHeight: '20px',
				color: '#4A4A4A',
				fontFamily: 'var(--app-font-quicksand-medium)',
			};
		},
		indicatorsContainer: (styles, { _data }) => ({
			...styles,
			marginTop: 'auto',
			marginBottom: 'auto',
			color: '#4A4A4A',
			size: 7,
			':hover': { color: 'gray' },
			width: '65px',
			height: '45px',
		}),
	};

	const taskEdit = () => {
		setLoading(true);
		let assignee_id = newAssignee ? newAssignee.assignee_id : assigneeId;
		updateTask(
			{
				assignee_id,
				name: taskName,
				description: taskDescription,
				due_date: moment(dueDate).format('YYYY-MM-DD'),
				status: taskStatus ?? '',
				add_info: { ...props?.task?.add_info, priority: priority ?? '' },
			},
			props?.task?.id
		).subscribe({
			next: _data => {
				setLoading(false);
				setDisplayModal(false);
			},
			error: error => {
				if (error) {
					setMessage(error.data);
					setLoading(false);
				}
			},
			complete: () => {},
		});
	};
	const removeTask = taskId => {
		setLoadingRemove(true);
		deleteTask(taskId).subscribe({
			next: _data => {
				setLoadingRemove(false);
				setToDeleteTask(false);
			},
			error: _error => {
				setLoadingRemove(false);
			},
			complete: () => {
				setDisplayModal(false);
			},
		});
	};

	const statusArray = [
		{ label: 'Done', value: 'Done' },
		{ label: 'Ongoing', value: 'Ongoing' },
		{ label: 'In Review', value: 'In Review' },
		{ label: 'Approved', value: 'Approved' },
		{ label: 'Postponed', value: 'Postponed' },
		{ label: 'Moved', value: 'Moved' },
		{ label: 'Cancelled', value: 'Cancelled' },
	];

	//console.log('task', task, user)
	return (
		<div className={cls.newTaskFormHost}>
			<Form style={{ flex: 1 }}>
				<div>
					<label style={{ color: message?.errors?.name ? 'var(--app-color-error)' : '', display: task.completed_at ? '' : '' }}>
						{'Name'}
						<textarea readOnly={task?.id === undefined ? false : task.completed_at ? true : user.id === task?.creator?.id ? false : true} required={message?.errors?.name} onFocus={() => setMessage('')} value={taskName} onChange={e => setTaskName(e.target.value)} />
					</label>
					{message && message?.errors?.name && <MessageContainer message={message.errors.name} />}
				</div>
				<div>
					<label style={{ color: message?.errors?.due_date ? 'var(--app-color-error)' : '' }}>
						{'Due Date'}
						<div>
							<DatePicker
								disabled={task?.id === undefined ? false : task.completed_at ? true : user.id === task?.creator?.id ? false : true}
								placeholderText="Due Date"
								customInput={
									<div
										id={'custom-input'}
										style={{
											cursor: 'pointer',
											display: 'flex',
											justifyContent: 'center',
											width: '100%',
											alignItems: 'stretch',

											border: '1px solid',
											borderColor: message?.errors?.due_date ? 'var(--app-color-error)' : 'var(--app-color-gray-lighter)',
										}}
									>
										<input
											readOnly={task?.id === undefined ? false : task.completed_at ? true : user.id === task?.creator?.id ? false : true}
											onFocus={() => setMessage('')}
											value={moment(dueDate).format('DD MMMM YYYY') + ' (' + moment(dueDate).fromNow() + ')'}
											type="text"
											required={message?.errors?.due_date}
											id={'due-date'}
											style={{
												flex: 1,
												marginTop: 'auto',
												marginBottom: 'auto',
												outline: 'none',
												border: 'none',
												fontFamily: 'var(--app-font-quicksand-medium)',
												fontSize: '16px',
												lineHeight: '20px',
												color: '#4A4A4A',
												textAlign: dueDate?.length > 0 ? 'center' : 'left',
												maxWidth: '98%',
											}}
											placeholder={'Due Date'}
											onChange={_e => {}}
										/>
										<Calendar className={cls.calendarSVG} />
									</div>
								}
								className={cls.datePickerWrapper}
								selected={dueDate}
								onChange={e => {
									setDueDate(e);
								}}
							/>
						</div>
					</label>
					{message && message?.errors?.due_date && <MessageContainer message={message.errors.due_date} />}
				</div>
				<div>
					<label style={{ color: message?.errors?.description ? 'var(--app-color-error)' : '' }}>
						{'Description'}
						<textarea disabled={task.completed_at ? true : false} onFocus={() => setMessage('')} required={message?.errors?.description} value={taskDescription} onChange={e => setTaskDescription(e.target.value)} />
					</label>
					{message && message?.errors?.description && <MessageContainer message={message.errors.description} />}
				</div>
				<div>
					<label style={{ color: message?.errors?.assignee_id ? 'var(--app-color-error)' : '' }}>
						{'Assignee'}
						{task.completed_at ? (
							<div>
								<input style={{ cursor: 'auto' }} placeholder={task?.assignee.first_name + ' ' + task?.assignee.last_name} readOnly />
							</div>
						) : (
							<Select
								onFocus={() => setMessage('')}
								isSearchable={false}
								key={'Assignee'}
								styles={customStyles}
								defaultInputValue={''}
								components={{ DropdownIndicator }}
								placeholder=""
								value={usersList[usersList.findIndex(c => c.value === newAssignee.assignee_id)]}
								options={usersList}
								onChange={position => {
									if (position) {
										setNewAssignee(c => ({ ...c, assignee_id: position.value }));
									}
								}}
							/>
						)}
					</label>
					{message && message?.errors?.assignee_id && <MessageContainer message={message.errors.assignee_id} />}
				</div>
				{props?.task?.creator && (
					<div style={{ marginTop: '10px' }}>
						<label>
							{'Creator'}
							<input style={{ cursor: 'auto' }} placeholder={task?.creator.first_name + ' ' + task?.creator.last_name} readOnly />
						</label>
						<label>
							{'Created Date'}
							<input style={{ cursor: 'auto' }} placeholder={moment(task?.created_at).format('DD MMMM YYYY') + ' (' + moment(dueDate).fromNow() + ')'} readOnly />
						</label>
					</div>
				)}
				<div style={{ marginTop: '10px' }}>
					<label style={{ color: message?.errors?.procedure_id ? 'var(--app-color-error)' : '', display: 'none' }}>
						Module Process
						{!isProcess && !isEdit ? (
							<Select
								onFocus={() => setMessage('')}
								isSearchable={false}
								key={'Process'}
								styles={customStyles}
								defaultInputValue={''}
								components={{ DropdownIndicator }}
								placeholder="Select process to start (optional)..."
								value={modulesList[modulesList.findIndex(c => c.module.procedure.id === taskProcess.process)]}
								options={modulesList}
								onChange={c => {
									if (c) {
										setTaskProcess({
											...companyProcesses.find(p => p.id === c.module.procedure.id),
											process: c.module.procedure,
											procedure_id: c.module.procedure.id,
											module_id: c.module.id,
											module: c.module,
											stepIndex: 0,
										});
										setIsModuleTask(true);
									}
								}}
							/>
						) : !isEdit ? (
							<p style={{ color: '#4a4a4a' }}>{selectedProcess.name}</p>
						) : (
							<input
								style={{ cursor: 'auto' }}
								placeholder={modulesList[modulesList.findIndex(c => c.module.procedure.id === taskProcess.process)]?.label ? modulesList[modulesList.findIndex(c => c.module.procedure.id === taskProcess.process)]?.label : 'Not a module process task.'}
								readOnly
							/>
						)}
					</label>
					<label style={{ color: message?.errors?.procedure_id ? 'var(--app-color-error)' : '' }}>
						Priority
						{task?.completed_at ? (
							<input style={{ cursor: 'auto' }} placeholder={task?.priority ?? 'Medium'} readOnly />
						) : (
							<Select
								onFocus={() => setMessage('')}
								key={'priority'}
								styles={customStyles}
								defaultInputValue={''}
								components={{ DropdownIndicator }}
								placeholder="Select priority (optional)..."
								value={priorityArray.find(s => s.value === priority)}
								options={priorityArray}
								onChange={e => setPriority(e.value)}
							/>
						)}
					</label>
					<label style={{ color: message?.errors?.procedure_id ? 'var(--app-color-error)' : '' }}>
						Status
						{task?.completed_at ? (
							<input style={{ cursor: 'auto' }} placeholder={task?.status ?? 'No status set'} readOnly />
						) : (
							<Select
								onFocus={() => setMessage('')}
								key={'status'}
								styles={customStyles}
								defaultInputValue={''}
								components={{ DropdownIndicator }}
								placeholder="Select status (optional)..."
								value={statusArray.find(s => s.value === taskStatus)}
								options={statusArray}
								onChange={e => setTaskStatus(e.value)}
							/>
						)}
					</label>

					{message && message?.errors?.procedure_id && <MessageContainer message={message.errors.procedure_id} />}
				</div>
				{message && message?.message && <MessageContainer message={message.message} />}
			</Form>
			{task?.completed_at ? (
				<div className={cls.buttonsContainer}>
					<Button clicked={onClose} className={cls.saveButton} title={'Close'} />
				</div>
			) : !isEdit ? (
				<div className={cls.buttonsContainer}>
					{!isMobile && <Button clicked={onClose} className={cls.saveButton} title={'Close'} />}
					{!isProcess && <Button light clicked={() => addTask(false)} className={cls.saveButton} title={'Save'} loading={loading} />}
					{isModuleTask && <Button light clicked={() => addTask(true)} className={cls.saveButton} title={'Run'} />}
				</div>
			) : (
				<div className={cls.buttonsContainer}>
					<Button clicked={onClose} className={cls.saveButton} title={'Close'} />
					<Button light clicked={() => setToDeleteTask(true)} className={cls.saveButton} title={'Delete'} />
					<Button light clicked={() => taskEdit()} className={cls.saveButton} title={'Update'} loading={loading} />
					{taskProcess.process && <Button light clicked={() => beginProcess()} className={cls.saveButton} title={'Run'} />}
				</div>
			)}

			{toDeleteTask && <DeleteTaskModal taskId={task.id} isOpen={toDeleteTask} removeTask={removeTask} loading={loadingRemove} onClose={setToDeleteTask} />}
		</div>
	);
}

const DeleteTaskModal = props => {
	const { isOpen, onClose, loading, removeTask, taskId } = props;
	return (
		<ModalR isOpen={isOpen} onClose={() => onClose(false)} backdropColor={true} blured={true} scrollable={true} shadowedContainerStyle={{ width: '35%' }}>
			<div>
				<p
					style={{
						textAlign: 'center',
						fontSize: '20px',
						color: '#4A4A4A',
						fontFamily: 'var(--app-font-poppins)',
					}}
				>
					{'Are you sure do you want to delete this task?'}
				</p>
				<Button loading={loading} clicked={() => removeTask(taskId)} title={'Yes, Delete'} style={{ fontWeight: '500', marginLeft: 'auto', marginRight: 'auto' }} />
				<p
					style={{
						cursor: 'pointer',
						textAlign: 'center',
						fontSize: '16px',
						fontWeight: 600,
						color: '#4A4A4A',
						fontFamily: 'var(--app-font-quicksand-medium)',
					}}
					onClick={() => onClose(false)}
					className={'go-back-text'}
				>
					{'No, Go Back'}
				</p>
			</div>
		</ModalR>
	);
};

const mapStateToProps = store => {
	const { selectedProcess, companyUsersArray, companyProcesses, allModules } = store.flowReducer;
	const { user } = store.sessionReducer;
	return { selectedProcess, companyUsersArray, companyProcesses, allModules, user };
};

export default connect(mapStateToProps)(TaskForm);
