import { Button, Spinner } from '../../common';
import cls from './ModulePanel.module.scss';

const ModulePanel = props => {
	const { iconSrc, cardTitle, children, style, endLabel = '', loading = false, onAddItem = () => {}, withAddButton = true } = props;
	return (
		<div className={cls.moduleBoardPanelHost} style={style}>
			<div className={cls.container}>
				<div className={cls.titleContainer}>
					{iconSrc && <img alt="" src={iconSrc} />}
					{cardTitle && <p className={cls.cardTitle}>{cardTitle}</p>}
					{loading && <Spinner type={'BallTriangle'} color="var(--app-color-gray-dark)" height={30} width={`${30}px`} style={{ padding: '0 0 0 10px' }} />}
				</div>
				{withAddButton && <Button clear style={{ width: '120px', backgroundColor: 'rgb(83, 159, 234)', color: 'white' }} clicked={onAddItem} title={'Add Item'} />}
				<div style={{ fontSize: '12px', textAlign: 'right' }}>{endLabel}</div>
				{children}
			</div>
		</div>
	);
};

export default ModulePanel;
