import moment, { now } from 'moment';
import { Observable } from 'rxjs';
import { setTransactions, setLastUpdate, addTransaction } from '../reduxStore/actions/flowActions';
import { store } from '../reduxStore/store';
import { URLS } from './API_CONSTANTS';
import { axiosInstance } from './axiosInstance';

export const createTransaction = (transactionData, module_Id, users = []) => {
	const transaction_items = transactionData?.items.map(function (el, index) {
		return {
			item: el.field_name,
			index: index,
			value: `${el.value}`,
			group: el.group,
			procedure_step_blueprint_field_id: el.procedure_step_blueprint_field_id,
		};
	});

	const newTransaction = {
		module_id: module_Id,
		items: transaction_items,
	};

	if (users?.length > 0) {
		newTransaction.users = users;
	}
	const {
		sessionSettings: { last_access_token },
	} = store.getState().authReducer;
	const config = {
		...URLS.transactions.createNewTransaction(last_access_token),
		data: newTransaction,
		forwardAllErrors: true,
	};

	return new Observable(subscriber =>
		axiosInstance(config)
			.then(response => {
				if (response?.status == 201 && response?.data?.data) {
					store.dispatch(addTransaction(response?.data?.data));
					subscriber.next(response?.data?.data);
					subscriber.complete();
				}
			})
			.catch(error => {
				subscriber.error(error);
			})
	);
};
export const editExistingTransaction = transactionData => {
	const transaction_items = transactionData?.items?.map(function (el) {
		return {
			index: el.index,
			item: el.field_name,
			value: `${el.value}`,
			group: el.group,
			procedure_step_blueprint_field_id: el.procedure_step_blueprint_field_id,
		};
	});
	const editedTransaction = {
		items: transaction_items,
	};
	const {
		sessionSettings: { last_access_token },
	} = store.getState().authReducer;
	const config = {
		...URLS.transactions.updateTransactionById(last_access_token, transactionData.id),
		data: editedTransaction,
		forwardAllErrors: true,
	};

	return new Observable(subscriber =>
		axiosInstance(config)
			.then(response => {
				if (response?.status === 201 && response?.data && response?.data?.data) {
					subscriber.complete();
				}
			})
			.catch(error => {
				subscriber.error(error);
			})
	);
};
export const updateTransaction = (transactionData, transactionId) => {
	const transaction_items = transactionData?.items?.map(function (el) {
		return {
			item: el.field_name,
			index: el.index,
			value: `${el.value}`,
			group: el.group,
			procedure_step_blueprint_field_id: el.procedure_step_blueprint_field_id,
		};
	});

	const updatedTransaction = {
		items: transaction_items,
	};

	const {
		sessionSettings: { last_access_token },
	} = store.getState().authReducer;
	const config = {
		...URLS.transactions.updateTransactionById(last_access_token, transactionId),
		data: updatedTransaction,
		handlerEnabled: true,
	};

	return new Observable(subscriber =>
		axiosInstance(config)
			.then(response => {
				if (response?.status === 200 && response?.data && response?.data?.data) {
					subscriber.next(response.data);
					subscriber.complete();
				}
			})
			.catch(error => {
				subscriber.error(error);
			})
	);
};
export const deleteTransaction = componentId => {
	const {
		sessionSettings: { last_access_token },
	} = store.getState().authReducer;
	const defaultConfig = { ...URLS.transactions.deleteTransactionById(last_access_token, componentId) };
	const config = {
		...defaultConfig,
		headers: { ...defaultConfig.headers },
	};
	return new Observable(subscriber =>
		axiosInstance(config)
			.then(response => {
				if (response?.status === 200 && response?.data) {
					subscriber.next(response.data);
					subscriber.complete();
				}
			})
			.catch(error => {
				subscriber.error(error);
			})
	);
};

export const getTransactions = (forced = false) => {
	const {
		sessionSettings: { last_access_token },
	} = store.getState().authReducer;

	const { lastUpdates, transactions } = store.getState().flowReducer;

	const config = {
		...URLS.transactions.getAllTransactions(last_access_token),
		handlerEnabled: true,
		//signal: axiosAbort.signal
	};

	//console.log('moment diff transactions', moment(now()).diff(moment(lastUpdates.transactions), 'minutes'), transactions.length);

	return new Observable(subscriber => {
		if (forced || moment(now()).diff(moment(lastUpdates.transactions), 'minutes') > 10) {
			axiosInstance(config)
				.then(response => {
					if (response?.status === 200 && response?.data && response?.data?.data) {
						store.dispatch(setLastUpdate({ ...lastUpdates, transactions: now() }));
						store.dispatch(setTransactions(response.data.data.length > 0 ? response.data.data : []));
						subscriber.next(response.data.data);
						subscriber.complete();
					}
				})
				.catch(error => {
					subscriber.error(error);
				});
		} else {
			subscriber.complete();
		}
	});
};
