import React from 'react';
import 'react-datepicker/dist/react-datepicker.css';
import { Button, ModalR } from '../../../common';
import TaskForm from '../TaskForm/TaskForm';
import { isMobile } from 'react-device-detect';
const TaskModal = props => {
	const { isEdit, modalVisible, heading, onClose, isProcess, setIsProcessBegin, task } = props;
	//console.log('taskModal', task)
	return (
		<div>
			<ModalR scrollable={true} shadowedContainerStyle={{ width: isMobile ? '96%' : '35%' }} isOpen={modalVisible} onClose={() => {}} blured={true}>
				<div>
					{isMobile && <Button clicked={onClose} title={'Close'} />}
					<p
						style={{
							fontFamily: 'var(--app-font-poppins)',
							fontSize: '2rem',
							color: '#4A4A4A',
							marginTop: '10px',
							marginBottom: '5px',
							textAlign: 'center',
						}}
					>
						{task.completed_at ? `Completed Task` : heading}
					</p>

					{isEdit && (
						<p
							style={{
								fontFamily: 'var(--app-font-poppins)',
								fontSize: '15px',
								fontWeight: '700',
								color: '#4A4A4A',
								marginTop: '5px',
								marginBottom: '5px',
								textAlign: 'center',
							}}
						>
							{`Task ID: ${task.id.slice(-6)}`}
						</p>
					)}

					<TaskForm task={task} setIsProcessBegin={setIsProcessBegin} setDisplayModal={onClose} isProcess={isProcess} isEdit={isEdit} onClose={() => onClose()} />
				</div>
			</ModalR>
		</div>
	);
};

export default TaskModal;
